.advantages__title {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: none;
  padding: none;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.advantages__list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, auto));
  grid-column-gap: 30px;
  padding-top: 65px;
  padding-bottom: 50px; }

.advantages__item {
  margin: 15px 0; }
  .advantages__item:hover .advantages__img {
    border-color: #e5493a; }
  .advantages__item:hover svg {
    fill: #e5493a; }
  .advantages__item:hover .advantages__subtitle {
    color: #e5493a; }
  .advantages__item:hover .advantages__text {
    color: #fff; }

.advantages__img {
  border-style: solid;
  border-width: 1px;
  border-color: #363636;
  border-radius: 50%;
  position: relative;
  width: 104px;
  height: 104px;
  transition-duration: 500ms;
  transition-property: border-color; }

.adventages__svg {
  fill: #363636;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 40px;
  transform: translate(-50%, -50%);
  transition-duration: 500ms;
  transition-property: fill; }

.advantages__subtitle {
  font-family: 'Raleway';
  font-size: 18px;
  color: #636363;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1.111;
  margin: 20px 0;
  transition-duration: 500ms;
  transition-property: color; }

.advantages__text {
  font-family: 'Roboto';
  font-size: 14pt;
  color: #555555;
  transition-duration: 500ms;
  transition-property: color; }
  .advantages__text::after {
    background-color: #e5493a;
    width: 70px;
    height: 2px;
    content: '';
    display: block;
    margin-top: 25px; }

@media (max-width: 480px) {
  .advantages__list {
    padding-top: 30px;
    padding-bottom: 25px; }
  .advantages__item {
    text-align: center; }
  .advantages__img {
    margin: 0 auto; } }

.works {
  background-color: #202020; }

.works__title-hide {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: none;
  padding: none;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.works__list {
  padding-top: 80px;
  padding-bottom: 160px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(12, minmax(60px, auto));
  grid-auto-rows: minmax(270, auto); }

.works__element--three {
  grid-column: span 3; }

.works__element--four {
  grid-column: span 4; }

.works__element--five {
  grid-column: span 5; }

.works__element--six {
  grid-column: span 6; }

.works__title {
  font-size: 30px;
  font-family: 'Raleway';
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.1;
  margin: 20px 0;
  position: relative; }
  .works__title::before {
    background-color: #fff;
    position: absolute;
    width: 70px;
    height: 2px;
    left: 0;
    top: -20px;
    content: ''; }

.works__text {
  color: #464646;
  font-size: 14px;
  line-height: 1.4;
