.works__element {
  position: relative; }
  .works__element:fitst-child {
    max-width: 270px; }
  .works__element::before {
    background-color: rgba(17, 17, 17, 0.85);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;
    content: '';
    transition-property: opacity;
    transition-duration: 500ms; }
  .works__element:hover::before {
    z-index: 1;
    opacity: 1; }
  .works__element:hover .works__info {
    border-color: #e5493a;
    opacity: 1;
    z-index: 2; }
  .works__element:nth-child(2) {
